import React from 'react';

import CardInfo from './CardInfo'

function Card(props) {

	return (
		<div className="d-inline-block k-card" onClick={() => props.click(props.item)}>
			<a href={props.item.link} target="_blank" rel="noopener noreferrer"><img className="k-card-image" src={props.item.imgSrc} alt={props.item.title} /></a>
			{ props.item.selected && <CardInfo title={props.item.title} subtitle={props.item.subTitle} link={props.item.link} />}
		</div>
	);

}

export default Card;