import React from 'react';
import {
  BrowserRouter as Router,
  Route,
  Link
} from "react-router-dom";

import { Container, Navbar, Nav } from 'react-bootstrap';

import './App.css';

import Footer from './components/Footer';
import HomePage from './pages/HomePage';
import AboutPage from './pages/AboutPage';
import ContactPage from './pages/ContactPage';

import kAvatar from './assets/images/avatar.png';


class App extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      title: 'ANDRÉ SILVA',
      imageSrc: kAvatar,
      headerLinks: [
        { title: 'Home', path: '/' },
        { title: 'About', path: '/about' },
        { title: 'Contact', path: '/contact' }
      ],
      home: {
        title: "ANDRÉ SILVA",
        subTitle: "Check my projects below"
      },
      about: {
        title: "ABOUT ME",
      },
      contact: {
        title: "LET'S TALK",
      }
    }
  }

  render() {
    return (
      <Router>
        <Container className="p-0" fluid={true}>
          <Navbar collapseOnSelect className="border-bottom" bg="transparent" expand="lg">
            <Navbar.Toggle className="border-0" aria-controls="navbar-toggle" />
            <Navbar.Collapse id="navbar-toggle">
              <Nav className="m-auto">
                <Link className="nav-link" to="/">Portfolio</Link>
                <Link className="nav-link">—</Link>
                <Link className="nav-link" to="/about">About</Link>
                <Link className="nav-link">—</Link>
                <Link className="nav-link" to="/contact">Contacts</Link>
              </Nav>
            </Navbar.Collapse>
          </Navbar>

          <Route path="/" exact render={() => <HomePage title={this.state.home.title} subTitle={this.state.home.subTitle} imageSrc={this.state.imageSrc} />} />
          <Route path="/about" render={() => <AboutPage title={this.state.about.title} />} />
          <Route path="/contact" render={() => <ContactPage title={this.state.contact.title} />} />

          <Footer />
        </Container>
      </Router>
    );
  }
}

export default App;
