import React from 'react';

import Hero from '../components/Hero'
import Content from '../components/Content'
import '../App.css';

function AboutPage(props) {

	return (
		<div>
			<Hero title={props.title} />

			<Content>
				<p>Hello, my name is André Silva. I'm a web developer with experience in Angular, React, Android, Java, MySQL, jQuery and more...</p>

				<p>My dream is to one day start my own business and became an entrepeneur (currently working in some ideas).</p>

				<p>I'm constantly learning new things, currently thos things incluide gaining more experience with MongoDB, React, Express, Node and Lua.</p>

				<p>My latest project, Vintage Watches, is an android app for selling rare watches. You can check it out <a href="https://github.com/korinku/vintage-watches-app" target="_balnk" rel="noopener noreferrer">here </a>, or on the homepage. Its build in Java using Android Studio and Firebase. However, I will rebuilding it using React or Kotlin for Android in the coming months.</p>

			</Content>

		</div>
	);

}

export default AboutPage;