import React from 'react';
import { Container, Jumbotron, Row, Col } from 'react-bootstrap';

function Hero(props) {

	return (
		<Jumbotron className="bg-transparent jumbotron-fluid p-0">
			<Container fluid={true}>
				<Row className="justify-content-center py-5 text-center">
					<Col md={6} sm={12}>
						<div className="circle">
							{props.imageSrc && <img className="k-avatar imageShadow" src={props.imageSrc} alt="teste" />}
						</div>
						{props.title && <h1 className="display-3 font-weight-bolder textShadow titleHome">{props.title}</h1>}
						{props.subTitle && <h3 className="display-6 font-weight-light">{props.subTitle}</h3>}
					</Col>
				</Row>
			</Container>
		</Jumbotron>
	);

}

export default Hero;